@import "styles/variables";
@import "styles/mixins";

.wrapper {
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  padding-bottom: 7.4rem;
  width: 26rem;
  z-index: 2;

  &::before {
    content: "";
    position: absolute;
    bottom: -1.5rem;
    right: 5rem;
    width: 3rem;
    height: 3rem;
    background-color: $colorHighlight;
    border-radius: 50%;
    transition-duration: $transitionDuration;
    z-index: 1;
  }

  &::after {
    content: "";
    position: absolute;
    top: 5rem;
    right: 0;
    width: 19.6rem;
    height: 19.6rem;
    border: 1px solid $colorHighlight;
    border-radius: 50%;
    z-index: 5;
  }

  &:hover {
    &::before {
      background-color: $colorLightest;
    }

    .description {
      transform: translateY(0);
    }
  }
}

.image {
  position: absolute;
  bottom: 0;
  left: -9rem;
  width: 25rem;
}

.label {
  @include timeline;

  span {
    color: $colorHighlight;
  }
}

.frame {
  position: absolute;
  top: 100%;
  left: 0;
  width: 45rem;
  height: 50rem;
  clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
}

.description {
  @include regular($colorLightest);

  position: absolute;
  top: 0;
  left: 0;
  margin: 0;
  padding: 2rem;
  width: 100%;
  background-color: $colorHighlight;
  border-radius: 2rem;
  transform: translateY(-100%);
  transition-duration: $transitionDuration;

  li {
    position: relative;
    padding-left: 2.6rem;

    &::before {
      --size: 1.2rem;
      content: "";
      position: absolute;
      top: 1rem;
      left: 0;
      width: var(--size);
      height: var(--size);
      background-color: $colorLightest;
      border-radius: 50%;
    }

    &:not(:first-child) {
      margin-top: 1.5rem;
    }
  }
}

.wrapperLast {
  .image {
    left: auto;
    right: 9rem;
  }

  .frame {
    left: auto;
    right: 0;
  }
}
