.section {
  background-color: lightgray;
  background-image: url("/assets/images/banner__bg.jpg");
  background-size: cover;
}

.wrapper {
  display: grid;
  grid-template-columns: 53rem 1fr;
  grid-column-gap: 20rem;
  height: 80rem;
}

.info {
  position: relative;
  padding-top: 23.5rem;
  z-index: 2;
}

.description {
  margin-top: 4.6rem;
}

.sideblock {
  display: flex;
  align-items: center;
  justify-content: center;
}
