@import "styles/variables";

.wrapper {
  --content-padding-left: 82rem;
  display: grid;
  grid-template-columns: 82rem minmax(0, 1fr);
  align-items: flex-start;
}

.heading,
.description,
.controls {
  grid-column: 2 / 3;
}

.description {
  margin-top: 4rem;
  padding-right: 13rem;
}
