@import "styles/variables";

.wrapper {
  position: relative;
  padding-bottom: 14rem;

  &::before {
    content: "";
    position: absolute;
    top: -9rem;
    left: 25rem;
    width: 54.8rem;
    height: 56rem;
    background-image: url("/assets/images/trafo_outline.png");
    background-size: cover;
    z-index: 1;
  }

  &::after {
    content: "";
    position: absolute;
    top: 4rem;
    left: 50rem;
    width: 31.3rem;
    height: 31.3rem;
    background-image: url("/assets/images/trafo_outline_mask.png");
    background-size: cover;
  }
}

.icon {
  position: absolute;
  top: 4rem;
  left: 50rem;
  width: 31.3rem;
  fill: $colorHighlight;
}