@import "styles/variables";
@import "styles/mixins";

.footer {
  @include regular;

  display: flex;
  align-items: center;
}

.list,
.item {
  display: flex;
  align-items: center;
}

.item {
  flex-direction: column;
  align-items: flex-start;

  &:not(:first-child) {
    margin-left: 12rem;
  }
}

.value {
  margin: 0;
}

.copyright {
  margin-left: auto;
}
