@import "styles/variables";

@keyframes rotateOuter {
  0% {
    transform: translateX(0);
  }

  25% {
    transform: translateX(2rem) translateY(2rem);
  }

  50% {
    transform: translateX(4rem) translateY(0);
  }

  75% {
    transform: translateX(2rem) translateY(-2rem);
  }

  100% {
    transform: translateX(0) translateY(0);
  }
}

@keyframes rotateInner {
  0% {
    transform: translateX(0);
  }

  25% {
    transform: translateX(-2rem) translateY(2rem);
  }

  50% {
    transform: translateX(-4rem) translateY(0);
  }

  75% {
    transform: translateX(-2rem) translateY(-2rem);
  }

  100% {
    transform: translateX(0) translateY(0);
  }
}

.wrapper {
  --spread: 50rem;
  --move-duration: 2s;
  position: relative;
  width: 100%;
  height: 100%;
  pointer-events: none;

  &::before {
    --size: 71rem;
    content: "";
    position: absolute;
    bottom: 2.5rem;
    right: calc(50% - var(--size) / 2 - 1rem);
    width: var(--size);
    height: var(--size);
    background-color: $colorOverlay;
    border-radius: 50%;
    box-shadow: 0 0 5rem $colorDarkest;
  }

  .iconOuter,
  .iconInner {
    position: absolute;
    bottom: 5rem;
    width: 65rem;
    transition-property: transform, opacity;
    transition-duration: var(--move-duration);
    opacity: 0;
    //mix-blend-mode: exclusion;
  }

  .iconOuter {
    left: calc(50% - 65rem / 2);
    transform: translateX(var(--spread));
    stroke: $colorLightest;
  }

  .iconInner {
    left: calc(50% - 65rem / 2);
    width: 65rem;
    transform: translateX(calc(-1 * var(--spread)));
    fill: $colorHighlight;
  }
}

.wrapperVisible {
  .iconOuter,
  .iconInner {
    transform: translateX(0);
    transition-timing-function: ease-in;
  }

  .iconOuter {
    opacity: 0.5;
  }

  .iconInner {
    opacity: 0.7;
  }
}

.wrapperShifted {
  .iconOuter,
  .iconInner {
    animation-duration: 15s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    animation-fill-mode: both;
  }

  .iconOuter {
    animation-name: rotateOuter;
  }

  .iconInner {
    animation-name: rotateInner;
  }
}
